* {
  margin: 0;
  padding: 0;
}

.typewriter {
  color: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: center;
}

h1 {
  font-size: 3rem;
  font-weight: 900;
}

#main {
  position: relative;
  height: 100vh;
  width: 100vw;
}

.cursor {
  animation: blink 1s linear infinite;
}

@keyframes blink {
  0% {
    opacity: 100%;
  }
  50% {
    opacity: 0%;
  }
}


